import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, Slides, LoadingController, Loading } from 'ionic-angular';
import { RoomService } from '../../model/room-service-model';
import { ToolsProvider } from '../../providers/tools/tools';
import { HotelServiceProvider } from '../../providers/hotel-service/hotel-service';
import { RoomsServiceProvider } from '../../providers/rooms-service/rooms-service';
import { RoomServiceExtraProvider } from '../../providers/room-service-extra/room-service-extra';
import { NotificationProvider } from '../../providers/notification/notification';
/**
 * Generated class for the RoomServicePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-room-service',
  templateUrl: 'room-service.html',
})
export class RoomServicePage {
  loading: Loading;
  @ViewChild('mainSlide') slides: Slides;
  @ViewChild('subSlide') tmpslides: Slides;
  roomServicesList: any = [];
  roomItems: any = [];
  filteredRoomItems: any = [];
  tmpServiceItems: any = [];
  currencySymbol: any = this.hotelProvider.objHotel.currency.signo; // for currency symbol
  Qty: any = [];
  total: number = 0;
  selectedId: string = "0";
  tempList: any = [];
  preSelected: any = [];
  showBackButton: boolean = true;

  constructor(private roomsServiceExtra: RoomServiceExtraProvider, public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController,
    private roomsService: RoomsServiceProvider, public toolsProvider: ToolsProvider,
    private hotelProvider: HotelServiceProvider, public alertCtrl: AlertController, private notificationProvider: NotificationProvider) {

    this.filteredRoomItems = this.roomsServiceExtra.filteredRoomItems;
    this.preSelected = this.roomsServiceExtra.preSelected;
    this.roomServicesList = this.roomsServiceExtra.roomServicesList;
    this.selectedId = this.roomsServiceExtra.selectedId;
    this.tempList = this.roomsServiceExtra.tempList;
    this.tmpServiceItems = this.roomsServiceExtra.tmpServiceItems;
    this.total = this.roomsServiceExtra.total;

  }

  ionViewDidLoad() {
    setTimeout(() => {
      this.slides.slideTo(this.roomsService.objRoomService.activeSlider, 0);
      this.tmpslides.slideTo(this.roomsService.objRoomService.activeSubSlider, 0);
    }, 200);

  }
  ionViewWillEnter() {
    this.showBackButton = true;
    if (this.roomsService.objRoomService && this.roomsService.objRoomService.currentOrderList.length > 0) {
      this.roomServicesList = this.roomsService.objRoomService.currentOrderList;
      this.total = this.roomsService.objRoomService.cartTotal;
      this.filteredRoomItems = this.roomsService.objRoomService.serviceItemsList.filter(x => x.servicioHabitacionId == parseInt(this.selectedId));
    }
  }

  ionViewWillLeave() {
    this.showBackButton = false;
    this.roomsService.objRoomService.activeSlider = this.slides.getActiveIndex();
    this.roomsService.objRoomService.activeSubSlider = this.tmpslides.getActiveIndex();
  }

  mainSlideNext() {
    this.slides.slideNext();
  }
  mainSlidePrev() {
    this.slides.slidePrev();
  }
  subSlideNext() {
    this.tmpslides.slideNext();
  }
  subSlidePrev() {
    this.tmpslides.slidePrev();
  }

  slideChanged(event) {
    if (!isNaN(event.getActiveIndex())) {
      this.selectedId = this.roomServicesList[this.slides.getActiveIndex()].id;    //gets value of id field of current slide
      this.filteredRoomItems = this.roomsService.objRoomService.serviceItemsList.filter(x => x.servicioHabitacionId == parseInt(this.selectedId));
      this.tmpslides.slideTo(0);
    }
  }

  //moves to previous slide
  slidePrev(value) {
    if (value == 1)
      this.slides.slidePrev();
    else
      this.tmpslides.slidePrev();
  }

  //to add quantity of a room service
  fnAddServiceItem(itemId) {
    //filter displayed array and add quantity 
    let tmpArray = this.filteredRoomItems.filter(x => x.id == itemId);
    tmpArray[0].quantity = tmpArray[0].quantity + 1;
    this.total = this.total + 1;

    //increase quantity in list to send to next page
    let tmpRoomServices = this.roomServicesList.filter(x => x.id == parseInt(this.selectedId)); //filter to get current slide's id
    let currentQty = tmpRoomServices[0].availableItems.filter(y => y.id == itemId)[0].quantity; //filter available items of the current slide using itemId
    currentQty = (currentQty) ? currentQty + 1 : 1;
    if (currentQty > 0) {
      tmpRoomServices[0].isSelected = true;
    }
    this.roomServicesList.filter(x => x.id == parseInt(this.selectedId))[0].availableItems.filter(y => y.id == itemId)[0].quantity = currentQty;    //placing new qty into list roomServicesList
    this.roomsService.objRoomService.serviceItemsList.filter(x => x.servicioHabitacionId == parseInt(this.selectedId)).filter(x => x.id == parseInt(itemId))[0].quantity = currentQty;  //placing new qty into list serviceItemsList
    this.roomsService.objRoomService.currentOrderList = this.roomServicesList;
    this.roomsService.objRoomService.cartTotal = this.total;
  }

  //to subtract quantity of a room service
  fnRemoveServiceItem(itemId) {
    //filter the displayed array and subtract quantity 
    let tmpArray = this.filteredRoomItems.filter(x => x.id == itemId);
    if ((tmpArray[0].quantity) && tmpArray[0].quantity > 0) {
      tmpArray[0].quantity = tmpArray[0].quantity - 1;
      this.total = this.total - 1;

      //decrease quantity in list to send to next page
      let tmpRoomServices = this.roomServicesList.filter(x => x.id == parseInt(this.selectedId)); //filter to get current slide's id
      let currentQty = tmpRoomServices[0].availableItems.filter(y => y.id == itemId)[0].quantity; //filter available items of the current slide using itemId
      currentQty = (currentQty) ? currentQty - 1 : 0;
      if (currentQty == 0) {
        tmpRoomServices[0].isSelected = false;
      }
      this.roomServicesList.filter(x => x.id == parseInt(this.selectedId))[0].availableItems.filter(y => y.id == itemId)[0].quantity = currentQty;  //placing new qty into list roomServicesList
      this.roomsService.objRoomService.serviceItemsList.filter(x => x.servicioHabitacionId == parseInt(this.selectedId)).filter(x => x.id == parseInt(itemId))[0].quantity = currentQty;  //placing new qty into list serviceItemsList
      this.roomsService.objRoomService.currentOrderList = this.roomServicesList;
      this.roomsService.objRoomService.cartTotal = this.total;
    }
  }

  //Loader
  public showLoading() {
    this.loading = this.loadingCtrl.create({
      spinner: 'crescent'
    });
  }

  //function to go to previous page
  public fnBack() {
    this.navCtrl.pop({ animate: true, animation: 'transition', duration: 500, direction: 'back' });
  }

  //function to go to page to view all selected items in cart and confirm order
  fnConfirmBooking() {
    this.roomsService.objRoomService.currentOrderList = this.roomServicesList;
    this.roomsService.objRoomService.cartTotal = this.total;
    if (this.roomsService.objRoomService.cartTotal > 0) {
      this.navCtrl.push("ConfirmOrderPage", {}, { animate: true, animation: 'transition', duration: 500, direction: 'forward' });
    }
    else {
      // error message translate
      let msg = this.toolsProvider.fnLanguageTranslate(["common.error", "view.servicioHabitacion.error.carritoVacio", "buttons.ok"]); //  message translation

      msg.subscribe((value: string) => {
        let alertnew = this.alertCtrl.create({
          title: value['common.error'],
          message: value["view.servicioHabitacion.error.carritoVacio"],
          cssClass: 'cls_alrt',
          buttons: [
            {
              text: value['buttons.ok'],
              role: 'cancel'
            }]
        });
        alertnew.present();
        setTimeout(() => { alertnew.dismiss(); }, 20000);
      });
    }
  }

}



